import { getHeaders } from "./getHeaders";
import { MediaHeaders, VideoType } from "../types/MediaTypes";

export const getVideoObject = (filename: string, url: string, headers?: MediaHeaders): Promise<VideoType> => {
    const data = {
        filename,
        type: "video",
        url,
    };

    if(headers) {
        return new Promise((resolve) => {
            resolve({
                ...data,
                headers
            })
        });
    }
    else {
        return getHeaders(url).then((headers) => {
            return {
                ...data,
                headers,
            }
        })
    }
};