import { sendList } from "../chrome/utils";
import { getVideoObject } from "../utils";
import { VideoType } from "../types/MediaTypes";

const send = function () {
    const videos = document.querySelectorAll('video');
    const list: VideoType[] = [];

    videos.forEach(video => {
        let src: string;
        if(video.src) {
            src = video.src
        }
        else {
            src = video.querySelector("source")?.src ?? "";
        }
        if(src) {
            getVideoObject(document.title,src).then((obj) => {
                list.push(obj);
                sendList({
                    list,
                    header: src,
                    id: src,
                });
            });
        }
    })
};

send();

chrome.runtime.onMessage.addListener((message) => {
    if (message.action === "pageUpdated") {
        send();
    }
});

let numberTab = "";
chrome.runtime.onMessage.addListener((message) => {
    if (message.action === "changeTab") {
        numberTab = message.tabID;
    }
    if (numberTab === message.tabID) {
        send();
    }
});
