import { TwitterResponseType } from "../../content/types";
import { MediaHeaders, SiteWithMediaType, VideoType } from "../../types/MediaTypes";

export type ChromeMessageType =
    DefaultMessageType |
    TwitterMessageType |
    PageUpdatedMessageType |
    TwitterResponseMessageType |
    ErrorMessageType |
    TikTokMessageType |
    TikTokResponseMessageType |
    YouTubeMessageType |
    YouTubeResponseMessageType;

export enum ActionType {
    Default = 'default',
    Twitter = 'twitter',
    PageUpdated = 'pageUpdated',
    TwitterResponse = 'twitterResponse',
    Error = 'error',
    TikTok = 'tikTok',
    TikTokResponse = 'tikTokResponse',
    YouTube = 'youTube',
    YouTubeResponse = 'youTubeResponse',
}

export type DefaultMessageType = {
    action: ActionType.Default;
    body: SiteWithMediaType;
}

export type TwitterMessageType = {
    action: ActionType.Twitter;
    body: {
        tweetId: string;
    };
}

export type PageUpdatedMessageType = {
    action: ActionType.PageUpdated;
}

export type TwitterResponseMessageType = {
    action: ActionType.TwitterResponse;
    body: TwitterResponseType;
}

export type ErrorMessageType = {
    action: ActionType.Error;
    error: string;
}

export type TikTokMessageType = {
    action: ActionType.TikTok;
    url: string;
}

export type TikTokResponseMessageType = {
    action: ActionType.TikTokResponse;
    data: {
        url: string;
        title: string;
        headers?: MediaHeaders;
    };
}

export type YouTubeMessageType = {
    action: ActionType.YouTube;
    url: string;
}

export type YouTubeResponseMessageType = {
    action: ActionType.YouTubeResponse;
    data: {
        list: VideoType[],
        header: string,
        id: string
    };
}
